import React from "react";
import { useState } from "react";
import * as p from "../style/PayMentStyle";
import PayComplete from "./PayComplete";

const Payment = ({setNext,title,price,img}) => {

    const [checked, setChecked] = useState(0);
    const [Complete,setComplete] = useState(false);
    return(
        <>
        <p.PayMentWrap>
            <p.BackIcon onClick={()=>setNext(false)}></p.BackIcon>
            <p.ItemDetailTop></p.ItemDetailTop>
            <p.address></p.address>
            <p.addressBox> </p.addressBox>
            <p.Gubun></p.Gubun>
            <p.PayMent></p.PayMent>
            <p.Bpay>
                <p.CheckBox onClick={()=>setChecked(0)} checked = {checked === 0 ? "checked" : ""}>
                    <p.CheckIcon></p.CheckIcon>
                </p.CheckBox>
                <p.PayTitle checked = {checked === 0 ? "checked" : ""}>B Pay</p.PayTitle>
                <p.PayBox color="#fff" bgColor="#5C63E5">
                    <p> <span>2,000,000</span></p>
                    <p> <span>{price}</span></p>
                </p.PayBox>
            </p.Bpay>
            <p.CreditCard>
            <p.CheckBox onClick={()=>setChecked(1)} checked = {checked === 1 ? "checked" : ""}>
            <p.CheckIcon></p.CheckIcon>
            </p.CheckBox>
                <p.PayTitle checked = {checked === 1 ? "checked" : ""}></p.PayTitle>
                <p.PayBox color="#666666" bgColor="#F8F8F8">
                    <p></p>
                    <p>1002-****-****-216107</p>
                    <p> <span>{price}</span></p>
                </p.PayBox>
            </p.CreditCard>
            <p.Cost><span></span><span>{price}</span></p.Cost>
            <p.Gubun></p.Gubun>
            <p.ItemArea>
                <p.ImgArea>
                    <img src={img} alt='' />
                </p.ImgArea>
                <p.TextArea>
                    <p>{title}</p>
                    <p>{price}</p>
                </p.TextArea>
            </p.ItemArea>
            <p.Btn onClick={()=>setComplete(true)}></p.Btn>
        </p.PayMentWrap>
        {Complete ? <PayComplete title={title} price = {price} img = {img} setComplete={setComplete}/> : ""}
        </>
    )
}

export default Payment;